<template>
  <div class="stars" :class="starType">
    <span class="star"  v-for="(itemCls,index) in clsArr" :class="itemCls"></span>
  </div>
</template>
<script type="text/javascript">
  // const cls_on = 'on'
  // const cls_half = 'half'
  // const cls_off = 'off'
  export default {
    props: ['size', 'score'],
    computed: {
        starType () {
          return 'star-' + this.size
        },
        clsArr () { // scope,star3
          let clsArr = [] // star,5
          let score = Math.floor(this.score * 2) / 2  // scope
          let intS = Math.floor(score) //  
          let half = score % 1 !== 0   // 
          for (let i = 0; i < intS; i++) {
            clsArr.push('on')      // On
          }
          if (half) {
            clsArr.push('half')
          }
          while (clsArr.length < 5) {  // 
            clsArr.push('off')
          }
          return clsArr
          }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '../../assets/stylus/mixin.styl'
  .stars
    font-size 0
    text-align:center
    .star
      display:inline-block
  .star-48
    .star
      width 20px
      height 20px
      margin-right 22px
      background-size 20px 20px
      &:last-child
        margin-right 0
      &.on
          bg-img('star48_on')
      &.half
          bg-img('star48_half')
      &.off
          bg-img('star48_off')
  .star-36
    .star
      width 15px
      height 15px
      margin-right 6px
      background-size 15px 15px
      &:last-child
        margin-right 0
      &.on
          bg-img('star36_on')
      &.half
          bg-img('star36_half')
      &.off
          bg-img('star36_off')
  .star-24
    .star
      width 10px
      height 10px
      margin-right 3px
      background-size 10px 10px
      &:last-child
        margin-right 0
      &.on
          bg-img('star24_on')
      &.half
          bg-img('star24_half')
      &.off
          bg-img('star24_off')


</style>



